@use "../../styles/partials/global";
@import "../../styles/partials/variables";
@import "../../styles/partials/mixins";

.navbar {
  display: flex;
  flex-direction: column;
  padding: 1.6rem;
  position: relative;
  @include tablet {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding-top: 0;
   
  }
  @include laptop{
    padding-left: 12rem;
    padding-right: 12rem;

  }

  &__logo {
    width: 12rem;
    height: 2.5rem;
    margin: 0 auto;
    @include tablet {
      margin-top: 1rem;
      margin-left: 3.6rem;
    }
  }
  &-input-img {
    margin-top: 1.6rem;
    display: flex;
    justify-content: space-between;
  }
  &__input {
    background-color: $albaster;
    height: 3.6rem;
    position: relative;
    display: flex;
    flex-direction: row;
    border-radius: 0.4rem;
    border: 0.1rem solid $mercury;
    max-width: 90%;
    @include tablet {
      height: 4rem;
      width: 100%;
    }

    &-searchIcon {
      position: absolute;
      width: 1.2rem;
      height: 1.2rem;
      margin-top: 1.1rem;
      margin-left: 0.8rem;
    }
    &-box {
      border: none;
      outline: none;
      background-color: transparent;
      padding: 0.6rem 6.4rem 0.6rem 2.4rem;
      
    }
  }
  &__userImg {
    width: 3.6rem;
    height: 3.6rem;
    border-radius: 50%;
    @include tablet {
      position: absolute;
      right: 3.6rem;
    }
    @include laptop{
      right: 12rem;
    }
  }
  &__button {
    position: relative;
    background-color: $brainflix-blue;
    margin-top: 1.6rem;
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    color: $white;
    border-radius: 0.4rem;
    text-align: center;
    transition: all .3s;
    &:hover{
      background-color: $brainflix-dark-blue;
    }
    @include tablet {
      width: 25%;
      margin-right: 8.6rem;
    }

    &--icon {
      width: 2.4rem;
      height: 2.4rem;
      z-index: 10;
    }
  }
  &__btn {
    cursor: pointer;
    background-color: transparent;
    border: none;
    outline: none;
    color: inherit;
    font-size: 1.6rem;
    font-weight: 400;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}
